import React, { Component } from "react";
import CardList from "./components/card-list/component-card-list";
import SearchBox from "./components/search-box/component-search-box";
import "./App.css";

class App extends Component {
  constructor() {
    super();
    this.state = {
      monsters: [],
      searchSField: "",
    };
    this.handleChange = this.handleChange.bind(this);
  }
  componentDidMount() {
    fetch("https://jsonplaceholder.typicode.com/users")
      .then((response) => response.json())
      .then((users) => {
        this.setState({ monsters: users });
      });
  }
  handleChange(e) {
    this.setState({ searchSField: e.target.value });
  }
  render() {
    const { monsters, searchSField } = this.state;
    const filteredMonsters = monsters.filter((monster) =>
      monster.name.toLowerCase().includes(searchSField.toLowerCase())
    );
    return (
      <div className="App">
        <h1>Say Hi to Monsters</h1>
        <SearchBox
          placeholder="filter by name"
          handleChange={this.handleChange}
        />
        <CardList monsters={filteredMonsters} />
      </div>
    );
  }
}

export default App;
