import React from "react";
import "./component-card.css";
function Card(props) {
  return (
    <div className="card-container">
      <img
        alt="monsters"
        src={`http://robohash.org/${props.monster.id}?set=set2&size=220x220`}
      />
      <h2>{props.monster.name}</h2>
      <p>{props.monster.email}</p>
    </div>
  );
}

export default Card;
